.header {
    position: fixed;

    z-index: 5;
}

.LightHead {
    background: rgb(148, 227, 246);
    background: linear-gradient(27deg,
            rgba(0, 136, 255, 0.848) 0%,
            rgba(36, 110, 212, 0.93) 26%, rgba(31, 163, 211, 0.9) 49%,
            rgba(18, 166, 220, 0.93) 76%, rgba(18, 175, 232, 0.939) 100%);
}

.DarkHead {
    background: rgb(78, 0, 172);
    background: linear-gradient(0deg, rgba(85, 0, 149, 0.8) 0%, rgba(79, 0, 110, 0.8) 100%);
}

.navInicio>button {
    display: flex;

    width: 50px;
    height: 50px;

    justify-content: center;
    align-items: center;

    object-fit: scale-down;

    background: none;
    border: none;
}

.navInicio>button>svg,
.navInicio>button>img {
    width: 100%;
    height: 100%;
}

.navInicio {
    justify-content: center;
    align-items: center;


    .titleSpan {
        font-family: 'Qwitcher Grypen', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

        font-size: 2.4rem;
        letter-spacing: 2px;

        color: rgb(215, 3, 162);
        filter: drop-shadow(1px 1px 2px #50020294);
        font-weight: 640;
    }


}

.WorldIs {
    transition: all 2s;
    background-color: rgba(12, 85, 175, 0.507);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

    filter: drop-shadow(1px 1px 2px #50020294);

}

.LightWorld>span:first-child {
    color: antiquewhite;
}

.LightWorld>span:last-child {
    background: rgb(9, 196, 178);
    background: linear-gradient(90deg, rgba(9, 146, 196, 0.782) 0%,
            rgba(59, 244, 241, 0.8295693277310925) 24%,
            rgb(3, 143, 236) 64%,
            rgb(0, 179, 255) 100%);

}

.DarkWorld>span:last-child {
    background: rgba(187, 9, 196, 0.8);
    background: linear-gradient(90deg, rgba(212, 6, 223, 0.607) 0%,
            rgba(196, 6, 239, 0.613) 24%, rgba(236, 3, 185, 0.613) 64%, rgba(221, 0, 255, 0.605) 100%);
}

.anim {
    animation: WorldIs_Anim 2s forwards;
    flex-grow: 1;
    letter-spacing: 1;
    overflow: hidden;
}

@keyframes WorldIs_Anim {
    0% {
        transform: scaleX(.4);
        letter-spacing: 1;
    }

    100% {
        letter-spacing: 4px;
        width: 100%;
    }
}


/*ESTRELLAS*/

.ContentStars {
    display: grid;
    padding: 0 12px;

    height: auto;

    grid-template-columns: auto 50px;
}

.ContentStars>div:first-child {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    overflow-x: auto;
}


.star {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    text-align: center;

    justify-content: center;
    align-items: center;

    margin: 4px;
    padding: 2px;

    min-width: 54px;
    width: auto;
    height: auto;

    letter-spacing: 1px;
    filter: drop-shadow(0 0 2px #000000dc) brightness(1.2);
    overflow: visible;
}

.star>input {
    appearance: none;
    width: 32px;
    height: 32px;
}

.star>input::before {
    position: absolute;
    top: -15px;

    font-size: 40px;
    content: "\2605";
}

.Colorido>.star>input::before {
    animation: arcoiris 10s infinite;
}


@keyframes arcoiris {
    0%{
        color: rgba(188, 0, 255, 1);
    }
    20%{
        color: rgb(0, 8, 255);
    }
    40%{
        color: rgb(255, 13, 0);
    }
    60%{
        color: rgb(0, 255, 26);
    }
    80%{
        color: rgb(0, 255, 208);
    }
    100%{
        color: rgb(0, 34, 255);
    }
}